export default {
	components: {
	},
	props: {
		value: {
			type: Number
		},
		list: {
			type: Array
		}
	},
	data() {
		return {
			showList: "",
			showClass: '',
			selectItem: {}
		}
	},
	watch: {
	},
	mounted() {
		this.showList = this.active;
		this.selectItem = this.list[this.value]
	},
	methods: {
		choose(item) {
			this.selectItem = item
			this.$emit('input', item.value)
			this.closePopup()
		},
		changePopup() {
			if(this.showList) {
				this.closePopup()
			} else {
				this.openPopup()
			}
		},
		openPopup() {
			// dropdown-select和dropdown-menu之间还有一层VNiView组件
			this.$parent.$parent.$emit('close')
			this.showList = true
			this.$nextTick(() => {
			  setTimeout(() => {
			    this.showClass = 'show'
			  }, 30)
			})
		},
		closePopup() {
			this.showClass = ''
			this.$nextTick(() => {
			  setTimeout(() => {
			    this.showList = false
			  }, 300)
			})
		},
		close() {
			this.showClass = ''
			this.showList = false
		}
	}
}
